	module.exports = {"data":{"markdownRemark":{"html":"<p>下海去游泳，浪很大，铺天盖地过来，把人打在下面。大家开玩笑说，现在知道什么叫灭顶之灾了。觉得很有挑战感。人生中要有勇气迎接挑战。我们大概是不像过去那样能够迎接挑战了。</p>\n<p>在这里，也觉得有片刻悠闲。得诗一首：</p>\n<p>得悠闲有感</p>\n<p>百思忽断到乐土，</p>\n<p>截袖开衣任踯躅。</p>\n<p>须发毵毵迎劲风，</p>\n<p>心绪颟顸散归途。</p>\n<p>一饮泉液比琼浆，</p>\n<p>数试薏苡赛醍醐。</p>\n<p>随易枯枝金沙下，</p>\n<p>闲躺浪蓐似糊涂。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0131.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0131.md","frontmatter":{"title":"8月1日 星期一"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0131.md absPath of file >>> MarkdownRemark"}}

